.live-diagram {
  margin: 200px auto 0;
  width: 1000px;
  .diagram-disk {
    background-color: #fff;
    background-image:
      radial-gradient(
        closest-side,
        transparent,
        transparent 50%,
        rgba(255, 255, 255, 0.12) 70%,
        rgba(255, 255, 255, 0.4)), 
      conic-gradient(
        from 270deg,
        #ff811f 45deg,
        #9000d8 45deg 135deg,
        #0d77cd 135deg 180deg,
        transparent 180deg);
    border-radius: 50%;
    height: 816px;
    margin: 0 auto;
    position: relative;
    text-align: center;
    width: 816px;
    .hovered-sector {
      border-radius: 50% / 43%;
      // box-shadow: 0 16px 10px -17px rgba(0,0,0,0.5);
      height: 440px;
      left: 119px;
      margin: 0 auto;
      overflow: hidden;
      position: absolute;
      top: 50%;
      transform: rotateZ(137.35deg);
      transform-origin: top center;
      width: 578px;
      &::after {
        background-color: #ee5bbc;
        background-image: linear-gradient(310deg, #ee5bbc 0%, #ee5bbc 50%, #ff3786 100%);
        border-left: 2px solid rgba(255, 255, 255, .45);
        border-top: 2px solid rgba(255, 255, 255, .45);
        // box-shadow: -1px -1px 10px -2px rgba(0,0,0,0.5);
        content: '';
        height: 408px;
        left: 84px;
        position: absolute;
        top: 216px;
        transform: rotate(45deg) skew(24.5deg, 24.5deg);
        width: 408px;
      }
      &:nth-child(2) {
        height: 434px;
        transform: rotateZ(224.75deg);
        &::after {
          background-image: linear-gradient(310deg, #9dce46 0%, #9dce46 50%, #7bcc62 100%);
          top: 257px;
          transform: rotate(45deg) skew(30.75deg, 30.75deg);
        }
      }
      .triangle {
        border-top: 18px solid #f93969;
        height: 0;
        position: absolute;
        width: 0;
        &:first-child {
          border-left: 35px solid transparent;
          left: 444px;
          top: 415px;
          transform: rotateZ(-110deg);
          transform-origin: top left;
        }
        &:nth-child(2) {
          border-right: 35px solid transparent;
          left: 82px;
          top: 390px;
          transform: rotateZ(-249deg);
          transform-origin: bottom right;
        }
      }
    }
